}
.card-box .card-title {
    color: #541d1d;
    font-size: 22px;
}
.card-box .card-text {
    margin-top: 8px;
    line-height: 1.3;
}
.card-box .card-image {
    margin-top: 10px;
}
.card-box .card-image .image-dialog-trigger {
      margin: 0;
      padding: 0;
      background: none;
      border: none;
      cursor: pointer;
}
.card-box .card-image .image-dialog-background {
      overflow: auto;
      position: fixed;
      z-index: 99;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      padding: 80px 40px;
      background-color: rgba(255, 255, 255, 0.9);
      text-align: center;
}
.card-box .card-image .image-dialog-background .image-dialog-close {
        position: absolute;
        right: 20px;
        top: 20px;
        width: 60px;
        height: 60px;
        padding: 0;
        background: none;
        border: none;
        cursor: pointer;
        -webkit-transition: 300ms ease-out;
        transition: 300ms ease-out;
        outline: none;
}
.card-box .card-image .image-dialog-background .image-dialog-close:hover {
          -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.card-box .card-image .image-dialog-background .image-dialog-close::before {
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          margin-top: -0.5px;
          margin-left: -20px;
          width: 40px;
          height: 1px;
          background-color: #000;
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.card-box .card-image .image-dialog-background .image-dialog-close::after {
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          margin-top: -0.5px;
          margin-left: -20px;
          width: 40px;
          height: 1px;
          background-color: #000;
          -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.card-box .card-image .image-dialog-background .image-dialog-animate {
        display: none;
        position: absolute;
        -webkit-transform-origin: left top;
        transform-origin: left top;
}
.card-box .card-image .image-dialog-background .image-dialog-animate.loading {
          display: block;
}
.card-box .card-image .image-dialog-background.dialog-leave-active {
        -webkit-transition: background-color 300ms ease-out;
        transition: background-color 300ms ease-out;
}
.card-box .card-image .image-dialog-background.dialog-leave-active .image-dialog-animate {
          display: block;
          -webkit-transition: -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: transform 300ms cubic-bezier(1, 0, 0.7, 1), -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
}
.card-box .card-image .image-dialog-background.dialog-leave-active .image-dialog-full {
          visibility: hidden;
}
.card-box .card-image .image-dialog-background.dialog-enter-active {
        -webkit-transition: background-color 300ms ease-out;
        transition: background-color 300ms ease-out;
}
.card-box .card-image .image-dialog-background.dialog-enter-active .image-dialog-animate {
          display: block;
          -webkit-transition: -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: transform 300ms cubic-bezier(1, 0, 0.7, 1);
          transition: transform 300ms cubic-bezier(1, 0, 0.7, 1), -webkit-transform 300ms cubic-bezier(1, 0, 0.7, 1);
}
.card-box .card-image .image-dialog-background.dialog-enter-active .image-dialog-full {
          visibility: hidden;
}
.card-box .card-image .image-dialog-background .dialog-leave-to {
        background-color: rgba(255, 255, 255, 0);
}
.card-box .card-image .image-dialog-background .dialog-enter {
        background-color: rgba(255, 255, 255, 0);
}









































































































